@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
   padding: 0;
   margin: 0;
   font-family: 'Poppins', sans-serif;
}
::-moz-selection {
   /* Code for Firefox */
   color: black;
   background: #95E0FF;
}
::selection {
   color: black;
   background: #95E0FF;
}
/* Loader */
@keyframes loaderAnimation {
   from {
      height: 0vh;
      width: 0px;
      border-radius: 50%;
   }
   to {
      height: 100vh;
      width: 100%;
      border-radius: 0;
   }
 }
.loader_main{
   height: 100vh;
   width: 100vw;
   background-color: white;
   position: fixed;
   display: flex;
   justify-content: center;
   align-items: flex-end;
   z-index: 9999999;
}
.loader_main div {
   animation: loaderAnimation .8s ease;
   height: 100vh;
   width: 100%;
   background-color: black;
   display: flex;
   justify-content: center;
   align-items: center;
}
/* Header */
nav{
   position: fixed;
   z-index: 999;
   width: 100%;
}
header {
   min-height: 70px;
   background-color: white;
   display: flex;
   align-items: center;
   flex-wrap: wrap;
   justify-content: space-between;
   padding: 0 40px;
   box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
header ul {
   display: flex;
   flex-wrap: wrap;
}
header label {
   display: flex;
   align-items: center;
   margin-right: 70px;
}
header label a {
   display: flex;
   text-decoration: none;
   align-items: center;
   font-size: 25px;
   font-weight: 700;
   color: #001780;
}
header label img {
   height: 50px;
   padding-right: 6px;
   margin-top: -12px;
}
header ul li {
   list-style: none;
   padding: 0 17px;
   border-radius: 12px;
   transition: all .2s ease;
   cursor: pointer;
}
header ul li:hover {
   background-color: #F9FBFF;
}
header ul li a {
   font-size: 16px;
   text-decoration: none;
   color: black;
   line-height: 70px;
   font-family: 'Roboto', sans-serif;
}
header ul li:hover a {
   color: #207de9;
}
header ul li:hover a svg {
   color: #207de9;
   transform: rotate(180deg);
}
header ul li a svg {
   margin-left: 5px;
}
header button {
   padding: 12px 32px;
   font-family: 'Roboto', sans-serif;
   background-color: #3d5af1;
   border-radius: 9px;
   color: white;
   cursor: pointer;
   border: 2px solid #3d5af1;
   font-size: 15px;
   box-shadow: 0 5px 10px -3px #3d5af1;
   transition: transform .2s ease, box-shadow .2s ease;
}
header button:hover {
   background-color: transparent;
   color: #3d5af1;
   transform: translateY(-3px);
   box-shadow: 0 10px 30px -8px #3d5af1;
}
/* Social kit */
.social_box {
   position: fixed;
   right: 0;
   margin-top: 290px;
   z-index: 1;
   box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
   border-top-left-radius: 28px;
   border-bottom-left-radius: 28px;
   overflow: hidden;
   padding: 9px 6px;
   background-color: white;
}
.social_box ul li {
   list-style: none;
   background-color: pink;
   border-radius: 50%;
   margin: 7px 0;
   display: flex;
   height: 40px;
   width: 40px;
   justify-content: center;
   align-items: center;
}
.social_box ul li:nth-child(1){
   background-color: #0A66C2;
}
.social_box ul li:nth-child(2){
   background-color: #FF0000;
}
.social_box ul li:nth-child(3){
   background-color: #1877F2;
}
.social_box ul li:nth-child(4){
   background-color: #F00075;
}
.social_box ul li:nth-child(5){
   background-color: #1877F2;
}
.social_box li a {
   font-size: 19px;
   color: white;
}
/* Home */
.home_main {
   height: 100vh;
   background-image: url(./images/bg/bgblue.webp);
   background-size: cover;
   background-position: center;
