.difference_main{
   padding: 90px;
   background: url(./images/bg/bgsvg1.svg) left center / contain no-repeat scroll padding-box border-box, url(./images/bg/bgsvg2.svg) right center / contain no-repeat scroll padding-box border-box, #181722;
